import React from 'react';

function AchievementCard({ image, title, description, links }) {
  return (
    <div className='achievements__card'>
      <div className='achievements__card--col1'>
        <img src={image} alt='' />
      </div>
      <div className='achievements__card--col2'>
        <h4>{title}</h4>
        <p>{description}</p>
        <div className='achievements__card--col2-links'>
          {links.map((e) => (
            <a href={e.link}>{e.title}</a>
          ))}
        </div>
      </div>
    </div>
  );
}

export default AchievementCard;
